import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import Img from 'gatsby-image';
import {Link} from 'gatsby';
import Text from 'views/Text/Text/v1/Text';
import IconButton from 'views/IconButton/v1/IconButton';
import {motion} from 'framer-motion';

const ACTIVE = 'active';

const renderRight = (props) => {
    if(props.switch || props.subitems){
        return (
            <li className="menu-right list">
                  <IconButton className="menu-icon" icon="icon-menu" color={'secondary'} fontSize={'2'} />
                  <ul className="dropdown">
                        {/* <div className="small">
                            {props.switch && props.switch.map((s, i) => {
                                return <li key={`large${i}`} className="list">{s}</li>;
                            })}
                        </div> */}
                        {/* {props.subitems && <div>
                            {props.subitems.map((s, i) => {
                                return <li key={`drop${i}`} className="list">{s}</li>;
                            })}
                        </div>} */}
                        {props.switch && props.switch.map((s, i) => {
                            return <li key={`large${i}`} className="dropdown-item list small">{s}</li>;
                        })}
                        {props.subitems.map((s, i) => {
                            return <li key={`drop${i}`} className="dropdown-item list side-item">{s}</li>;
                        })}
                  </ul>
            </li>
        )
    }

}
const Menu = (props) => {
    return <Bar id={props.id} key={props.id} className={`noscriptFadeIn ${props.active?'show':'hide'}`} 
                                    initial="hidden"
                                    animate={props.active?'visible':'hidden'}
                                    variants={PAGE_VARIANTS}
                                  >
                    {props.menuIcon}
                  <div className="heading-list list-item">
                    <Link to="/" className="navbar-item hoverable" activeClassName={ACTIVE} aria-label="go to home page">
                      <Text tag="h2">{props.title}</Text>
                    </Link>
                  </div>
                  <ul className="main-list">
                  {props.main && props.main.map((s, i) => {
                        return <li key={`main${i}`} className="main-item list-item">{s}</li>;
                    })}
                  </ul>
                  <ul className="mini-list">
                  {props.mini && props.mini.map((s, i) => {
                        return <li key={`mini${i}`} className="mini-item list-item">{s}</li>;
                    })}
                  </ul>
            </Bar>
    
}
 
const PAGE_VARIANTS = {
  visible: { opacity: 1, y: 0, transition: {delay: 0.5, duration: 0.3}},
  hidden: { opacity: 0, y:-100, transition: {delay: 0, duration: 0.3} },
}


Menu.propTypes = {
  left: PropTypes.array,
  right: PropTypes.array,
  subitems: PropTypes.array,
  switch: PropTypes.array,
  active: PropTypes.bool,
  title: PropTypes.string,
  menuIcon: PropTypes.object,
  img: PropTypes.object,
  alt: PropTypes.string,
  color: PropTypes.string
}

//export default MatchActive(Menu);
export default Menu;
//const Bar = styled(motion.div)`

const Bar = styled.div`
  height: ${props => props.theme.menuHeight}px;
  background-color: ${props => props.theme.neutralD};

  left: 0; top: 0; right: 0;
  z-index: ${props => props.theme.zTop};
  .list{
    transition: background-color 1s ease;
    
    z-index: ${props => props.theme.zTop};
  }
  .heading-list, .main-list{
    display:inline-block;
  }

  .main-list{
    margin:0;
    .main-item{
      color: pink;
    }
  }
  .mini-list{
    display: block;
    margin:0px;
    position: relative;
    top: -20px;
    text-align:center;
    li{
      color: yellow;
      list-decoration:none;
      display: inline;
    }
  }
  .list-item{
    list-decoration:none;
    display: inline-block;
    padding: 3px;
    a{
      color: transparent
    }
    a.active{
      color: ${props => props.theme.primary};
    }
  }
  .title-img{
    display: none !important;
  }
  @media (min-width: 250px){
    .title-img{
      display: inline-block  !important;
    }
  }
  @media (min-width: ${props => props.theme.mediaM}){
    .mini-list li{
    }
  }
`

