import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import {Glow, Highlight} from './Text.css.js';
import './styles.css';
import {log} from 'utilities/Measure.js';

const EMPTY = {};
export default class Text extends React.Component{
    constructor(props){
        super(props);
        if(Array.isArray(props.text)){
            throw Error(`invalid text array for ${props.id} passed to Text view. Pass this to a text manager`)
        }
        this.handleItemClick = this.handleItemClick.bind(this);
        this.state = {
            onClick: {onClick: this.handleItemClick}
        }
        this.wrapText = this.wrapText.bind(this);
        this.makeGlow = this.makeGlow.bind(this);
        this.getClick = this.getClick.bind(this);
        
    }
    handleItemClick(ev){
        //get the name of the item
        if(this.props.onInput){
            this.props.onInput(this.props.id, this.props.out);
        }else{
            this.props.viewProps.onInput(this.props.id, this.props.out);
        }
        
    }
    getClick(){
        if(this.props.out) return this.state.onClick;
        if(this.props.onInput) return this.state.onClick;
        return EMPTY
    }
    wrapText(children){
        switch(this.props.tag){
            case 'a':
                return <A  {...this.getClick()} {...this.props} 
                className={`${this.props.className || 'inline text-center '}`} key={this.props.id} 
                 target="_blank"
                >{children}</A>
            case 'h1':
                return <H1 {...this.getClick()} {...this.props} className={`${this.props.className || 'inline text-center '}`}  key={this.props.id} >{children}</H1>
            case 'h2':
                return <H2  {...this.getClick()} {...this.props} className={`${this.props.className || 'inline text-center '}`}  key={this.props.id} >{children}</H2>
            case 'h3':
                return <H3  {...this.getClick()} {...this.props} className={`${this.props.className || 'inline text-center '}`}  key={this.props.id}>{children}</H3>
            case 'h4':
                return <H4  {...this.getClick()} {...this.props} className={`${this.props.className || 'inline text-center '}`}  
                key={this.props.id} {...this.props}>{children}</H4>
            case 'h5':
                return <H5  {...this.getClick()} {...this.props} className={`${this.props.className || 'inline text-center '}`}  
                key={this.props.id} {...this.props}>{children}</H5>
            case 'p':
            default:
                return <P {...this.getClick()} {...this.props} className={`${this.props.className || 'inline text-center '}`}
                key={this.props.id} >{children}</P>
        }
    }
    makeGlow(children){
        if(this.props.glow){
            return <div className={`glow-parent ${this.props.className || ''}`}>
            <Glow className="glow bg-glow" glow={this.props.glow}>{children}</Glow>
            <span className="glow top-glow" >{children}</span>
        </div>
        }else if(this.props.highlight){
            return <div className={`glow-parent ${this.props.className || ''}`}>
            <Highlight className="glow bg-glow" glow={this.props.highlight}>{children}</Highlight>
            <span className="glow top-glow" >{children}</span>
        </div>
        }
        return children;
    }
   
    render(){
        
        return (
            this.makeGlow(this.wrapText(this.props.hasOwnProperty('text')?this.props.text:this.props.children))
        )
    }
}

Text.propTypes = {
    tag: PropTypes.oneOf(['p', 'h1', 'h2', 'h3', 'h4', 'h5', 'h6', 'a']),
    text: PropTypes.string.isRequired
}

const getStyles = (props) => {
    let s = `
    ${props.margin? `margin: ${props.margin};`:`margin: 0px auto;`}
    ${props.padding? `padding: ${props.padding};`: `padding:5px;`}
    ${props.fontSize?`font-size:${props.theme.getFontSize(props.theme, props.fontSize, 'remS')} !important;`:''}
    ${props.lineHeight?`line-height:${props.lineHeight};`:''}
    
    ${props.color?`color: ${props.theme.getColor(props.theme, props.color, 'text')};`:''}
    ${props.backgroundColor?`background-color: ${props.theme.getColor(props.theme, props.backgroundColor, 'text')};`:''}

    `;
    return s;
}
//${props.maxWidth?`max-width ${props.maxWidth};`:`max-width: ${props.theme.mediaL};`}
//${props.fontSize?`font-size:${props.theme.getFontSize(props.theme, props.fontSize, 'remS')} !important`:''}
const A = styled.a`
    ${props => getStyles(props)}
    text-decoration: underline;
`

const H3 = styled.h3`
    font-size: 1em;
    @media (min-width: ${props => props.theme.mediaM}){
        font-size: 1.7em;
    }
    color: ${props => props.theme.primary};
    ${props => getStyles(props)}
`
const H2 = styled.h2`
    font-size: 1.3em;
    @media (min-width: ${props => props.theme.mediaM}){
        font-size: 2em;
    }
    color: ${props => props.theme.primary};
    ${props => getStyles(props)}
`

const H1 = styled.h1`
    font-size: 1.5em;
    @media (min-width: ${props => props.theme.mediaM}){
        font-size: 2.6em;
    }
    color: ${props => props.theme.primary};
    ${props => getStyles(props)}
`
const H4 = styled.h4`
    font-size: 1.1em;
    @media (min-width: ${props => props.theme.mediaM}){
        font-size: 1.5em;
    }
    color: ${props => props.theme.primary};
    ${props => getStyles(props)}
`
const H5 = styled.h5`
    font-size: 0.8em;
    @media (min-width: ${props => props.theme.mediaM}){
        font-size: 1em;
    }
    color: ${props => props.theme.secondary};
    ${props => getStyles(props)}
`
const P = styled.p`
    font-size: 0.85em;
    @media (min-width: ${props => props.theme.mediaM}){
        font-size: 1em;
    }
    color: ${props => props.theme.text};
    ${props => getStyles(props)}
`
