import React from 'react';
import PropTypes from 'prop-types';
import Menu from 'views/Menu/index.js';
import {Link} from 'gatsby';
import Text from 'views/Text/Text/v1/Text';
import Img from 'gatsby-image';
import './styles.css';
import {motion} from 'framer-motion';


const ACTIVE = 'active';

const Main = [

<Link key="/blog/" to="/blog/" activeClassName={ACTIVE} className="navbar-item hoverable" aria-label="go to blog index page">
  <Text tag="h4" color="secondary"  margin="0" bold>Blog</Text>
</Link>, 
    <Link key="/games/" to="/mathgames/" activeClassName={ACTIVE} className="navbar-item hoverable" aria-label="go to games page">
    <Text tag="h4" color="secondary" margin="0" bold>Games</Text></Link>];
const MRight = [

];

const Mini = [
  <Link key="/account/" to="/account/" activeClassName={ACTIVE} className="navbar-item hoverable" aria-label="go to account page">
  <Text tag="h4" color="secondary" fontSize="remS"  bold>Account</Text></Link>,
  <Link key="/about/" to="/about/" activeClassName={ACTIVE} className="navbar-item hoverable" aria-label="go to about page">
  <Text tag="h4" color="secondary" fontSize="remS"  bold>About</Text></Link>,
  <Link key="/privacy/" to="/privacy/" activeClassName={ACTIVE} className="navbar-item hoverable" aria-label="go to privacy page">
  <Text tag="h4" color="secondary" fontSize="remS" bold>Privacy</Text></Link>
]

const WebsiteMenu = (props) => {

    return <motion.div //initial="hidden" // animate={props.active?'visible':'hidden'} variants={variants}
          className="noscriptFadeIn "
          ///{...animations[animKeys.ANIM_UP]}
          {...SLIDE_UP}
        >
        <Menu {...props} title={props.meta.title}
      menuIcon={<Img
        id="title-img"
        className="title-img my-nav-style"
        fixed={props.img.fixed}
        title={props.meta.iconAlt}
        alt={props.meta.iconAlt}
      />} 
      subitems={MRight} main={Main} mini={Mini} />
    </motion.div>
}
 
const SLIDE_UP = {
  initial: {
      opacity: 0, y: -100, 
  },
  animate: {
      opacity: 1, y: 0,  
  },
  exit: {
      opacity: 0, y: -100, 
  },
  transition: {
    duration: 0.3, ease: 'easeInOut'
}
}
WebsiteMenu.propTypes = {
  left: PropTypes.array,
  right: PropTypes.array,
  switch: PropTypes.array,
  active: PropTypes.bool,
  meta: PropTypes.object,
  img: PropTypes.object,
  //alt: PropTypes.string,
  color: PropTypes.string
}

export default WebsiteMenu;

