import React from 'react';
import PropTypes from 'prop-types';
import Text from 'views/Text/Text/v1/Text.js';
import {sendError} from 'utilities/Measure.js';
import {ErrorDisplay} from './Boundary.css';
import {log} from 'utilities/Measure';

class ErrorBoundary extends React.Component {
    constructor(props) {
        super(props);
        //this.back = this.back.bind(this);
        this.state = { hasError: false };
    }
    componentDidUpdate(prevProps, prevState){
        if(this.props.error && (this.props.error !== prevProps.error)){
            sendError(`Error caught by ${this.props.id}: ${this.props.error}`, false);
        }
    }
    static getDerivedStateFromError(error) {
        // Update state so the next render will show the fallback UI.
        return { hasError: true };
    }
    
    componentDidCatch(error, errorInfo) {
        sendError(`Error caught by ${this.props.id}: ${error} stack: ${errorInfo.componentStack}`, false);
    }
    render() {
        if (this.state.hasError || this.props.error) {
            // You can render any custom fallback UI
            return <ErrorDisplay>
                {this.props.errorView}
            </ErrorDisplay>;
        }

        return this.props.children; 
    }
}

ErrorBoundary.propTypes = {
   // onBack: PropTypes.func.isRequired, //used to go back to menu on error
    error: PropTypes.string,
    id: PropTypes.string.isRequired,
    errorView: PropTypes.array.isRequired
}

export default ErrorBoundary;

