import {sendError, log} from 'utilities/Measure.js';

if(typeof window !== 'undefined'){
    window.onerror = (msg, src, lineno, colno, err) => {
        
     //   let message = `msg: ${msg}, src: ${src}, lineno: ${lineno}, colno: ${colno}`;
        log(`window.onerror called end ${process.env.GATSBY_MY_ENV} message`, msg)
        if(process.env.GATSBY_MY_ENV === 'production'){
            //send general to google
            sendError(msg, true);
        }else{
            //print details to console
            log(`%c ` + msg,  err, 'error', "color: pink");
        }
        return true;
    }
}