//ANIM 
export const FADE = {
    initial: {
        opacity: 0,  transition: {
            delay: 1, when: "beforeChildren",
            staggerChildren: 0.3,
        }
    },
    animate: {
        opacity: 1,  transition: {
            delay: 1, when: "beforeChildren",
            staggerChildren: 0.3,
        }
    },
    exit: {
        opacity: 0, transition: {
            when: "afterChildren",
          },
    },
};
export const SLIDE_UP = {
    initial: {
        opacity: 0, y: -100, 
     
        transition: {
            delay: 0.5, duration: 0.5, ease: 'easeInOut'
        }
    },
    animate: {
        opacity: 1, y: 0,  
        transition: {
            delay: 0.5, duration: 0.5, ease: 'easeInOut'
        }
    },
    exit: {
        opacity: 0, y: -100, 
        transition: {
            delay: 0, duration: 0.5, ease: 'easeInOut'
        },
    },
}


export const FIFO = {
    initial: {
        opacity: 0,
       // y: 100,
        position: 'absolute',
        transition:{delay: 2, dur: 1, when: "beforeChildren", staggerChildren: 0.3,  delay: 0.3}
    },
    animate: {
        opacity: 1,
        y:0,
        position: 'relative', 
        transition:{delay: 2, dur: 1, when: "beforeChildren", staggerChildren: 0.3, delay: 0.3}
    },
    exit: {
        opacity: 0,
        //y: 100,
        position: 'absolute', 
        transition: {delay: 0, dur: 0.3, 
            when: "afterChildren",staggerChildren: 0.3,
          },
    },
}
