import React from 'react';
//import './styles.css';

export default class NoScript extends React.Component{
    render(){
        return <noscript>
        <div className="fadeInNoScriptStyled bg-text" style={{position: 'fixed', left: '0', bottom: '40px', right: '0',  
        margin: '0', backgroundColor: 'rgba(0,0,0,0.8)', padding: '10px' ,  textAlign: 'center', 
         animation: `fadeInNoScript 2s ease 0.3s forwards`}}>
           <p  class="noscript" style={{fontSize: '1.2em', color:'yellow'}}>
            Some features of this website require javascript    
          </p>
        </div>
    </noscript>
    }
}