import React, {useEffect} from 'react';
 import styled from 'styled-components';
import IconButton from 'views/IconButton/v1/IconButton';
import Text from 'views/Text/Text/v1/Text';
import {motion, AnimatePresence} from 'framer-motion';
import {animations, keys as animKeys} from 'animations';
import {log} from 'utilities/Measure';

const SLIDE_UP = {
    initial: {
        opacity: 0, y: -100, 
    },
    animate: {
        opacity: 1, y: 0,  
    },
    exit: {
        opacity: 0, y: -100, 
    },
    transition: {
      duration: 0.3, ease: 'easeInOut'
  }
}

const Menu = (props) => {
    console.log(`Game Appbar props`, props);
    const renderMenuItems = () => {
        let items = [];
        let item;
        for(let k in props.menuItems){
            item = props.menuItems[k];
            if(item){
                items.push(<motion.span className="hoverable" key={k} 
                //{...animations[animKeys.ANIM_UP]} 
                {...SLIDE_UP}
                >
                {item}
            </motion.span>);
            }
            
        }
        return items;
    }
    return(
            <Bar key="gamemenubar" id="gamemenubar" className="noscriptFadeIn"  
            //{...animations[animKeys.ANIM_UP]}
            {...SLIDE_UP}
            >
                
                {props.onBack && <IconButton className="left back" fontSize="iconS" height="100%" color="text" rotate={90}
                    icon={`icon-circle-down`} 
                    onInput={props.onBack}
                    />}
                <Text tag="h1" className="hoverable menu-item menu-title">{props.title}</Text>
                {/* <ItemManager id="view-menu-items" className="right" wrappercomponent={<div />} /> */}
                <div id="view-menu-items" className="right">
                    {props.children}
                    {/* <AnimatePresence exitBeforeEnter>
                        {props.menuItems && renderMenuItems()}
                    </AnimatePresence> */}
                     {props.menuItems && renderMenuItems()}
                </div>
            </Bar>
    )
}
const variants = {
    visible: { opacity: 1 },
    hidden: { opacity: 0 },
}

const PAGE_VARIANTS = {
    initial: {opacity: 0, y: -100},
    exit: {opacity: 0, y: -100},
    animate: {opacity: 1, y:0},
    transition: {delay: 0, duration: 0.3}
}
export default Menu;
// //background-color: ${props => props.theme.neutral};
//height: ${props => props.theme.gameMenuHeight}px;
const Bar = styled(motion.nav)`
    background-color: ${props => props.theme.neutralD};
    height: ${props => props.theme.menuHeight}px;
    margin: 0;
    position: relative;
    max-width: 100%;
    z-index:9;
    #view-menu-items{
  
    }
    button, span button{
        z-index: 99;
        display: inline;
        vertical-align: middle;
    }
    .left{
        float: left;
    }

    .menu-title{
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        position: absolute;
        margin: 0px;
        width: 100%;
        left: 0px;
        text-align:left;
        padding: 0px 0px 0px 56px;
        color: ${props => props.theme.text};  
        line-height: ${props => props.theme.gameMenuHeight}px;
        vertical-align: bottom;
        margin: 0;
        display: inline-block;
        font-size: ${props => props.theme.remS};
     
    }
    .menu-item{
        padding-right: 10px;
        color: ${props => props.theme.text};  
        line-height: ${props => props.theme.gameMenuHeight}px;
        vertical-align:middle;
    }
    #view-menu-items span{
        vertical-align:middle;
        height:100%;
    }
    span p{
        line-height:  ${props => props.theme.gameMenuHeight}px;
        display: inline;
        margin: 0;
        vertical-align: top;
    }
    a {
        display: inline-block;
        position: absolute;
        top: 0;
        height: ${props => props.theme.gameMenuHeight}px;
        vertical-align: bottom;
        padding: 0px 12px;

        h1{


        }
    }
    .right{
        visibility: visible;
        position: absolute;
        top: 0px;
        right:0px;
        opacity: 1;
        height:100%;
        transform: translateY(0%) scaleY(1);
        float: right;
        padding-right: 10px; margin: 0;

 
        span{
            display: inline-block;
            background-color: ${props => props.theme.neutralD};
            
        }
    }
    @media (min-width: ${props => props.theme.mediaS}), 
    @media only screen and (min-width: ${props => props.theme.mediaS}),
    @media only screen and (min-device-width: ${props => props.theme.mediaS}) {
      .menu-title{
        font-size: ${props => props.theme.remS};
      }
    }
`
/**
 * 
 * 
 *         span, p, h1{
            opacity: 0.8;
            transition: opacity 0.5s ease;
            &:hover{
                opacity: 1;
            }
        }
 */