import styled from 'styled-components';

export const Glow = styled.span`
    position: absolute;
    top: 0px;
    left: 0px; right: 0px;
    color: transparent;
    width:100%; 
    height:100%;
    ${props => {
        return `text-shadow: 0 0 4px #fff, 0 0 5px #fff, 0 0 6px ${props.theme.getColor(props.theme, props.glow)}, 
        0 0 6px ${props.theme.getColor(props.theme, props.glow)}, 0 0 8px ${props.theme.getColor(props.theme, props.glow)}, 
        0 0 10px ${props.theme.getColor(props.theme, props.glow)}, 0 0 15px ${props.theme.getColor(props.theme, props.glow)};`
    }}
    animation: glowO 9000ms ease 0ms infinite alternate;
`
export const Highlight = styled.span`
    position: absolute;
    top: 0px;
    left: 0px; right: 0px;
    color: transparent;
    opacity: 0.6;
    ${props => {
        return `text-shadow: 0 0 1px #fff, 0 0 1px #fff, 0 0 3px ${props.theme.getColor(props.theme, props.highlight)}, 
        0 0 3px ${props.theme.getColor(props.theme, props.highlight)}, 0 0 4px ${props.theme.getColor(props.theme, props.highlight)}, 
        0 0 6px ${props.theme.getColor(props.theme, props.highlight)}, 0 0 8px ${props.theme.getColor(props.theme, props.highlight)};`
    }}
`
