import React from "react";
import PropTypes from 'prop-types';
  import './../styles/style.css';
 import './IconButton.css';
 import Text from 'views/Text/Text/v1/Text.js';
 import {Icon, Btn} from './IconButton.css.js';

class IconButton extends React.Component{
    constructor(props){
        super(props);
        this.handleClick = this.handleClick.bind(this);
        this.makeGlow = this.makeGlow.bind(this);
        //this.state = {mounted: false};
    }

    handleClick(){
        if(this.props.onInput){
            this.props.onInput(this.props.id, this.props.out, this.props.viewProps, this.props.mergeInput);
        }else if(this.props.mergeInput){
            this.props.mergeInput(this.props.id, this.props.out);
        }
    }
    makeGlow(){
        return <Icon className={`${this.props.icon} rotates ${this.props.text?'text-icon':''} `} 
                rotate={this.props.rotate}  color={this.props.color} iconSize={this.props.iconSize}
            disabled={this.props.disabled?this.props.disabled:false} fontSize={this.props.fontSize} 
            />;
    }
    makeButton(view){
        return <Btn key={this.props.id} id={this.props.id} rotate={this.props.rotate}  
                    width={this.props.width} height={this.props.height} padding={this.props.padding}
                    round={this.props.round} //glow={this.props.glowBtn} 
                    hover={this.props.hover}
                    hoverScale={this.props.hoverScale}
                    bgColor={this.props.bgColor}
                    top={this.props.top}
                    disabled={this.props.disabled?this.props.disabled:false} style={this.props.style}
                    className={`glyph fs1 ${this.props.className?this.props.className:''}`} 
                    onClick={this.handleClick}>
                    {view}
                </Btn>
    }
    render(){
        
        return this.makeButton(<div >
        {this.makeGlow()}
        {this.props.text && <Text tag="p" className="icon-text inline-block" margin="0px auto 0px auto" 
                color={this.props.color} >{this.props.text}</Text>
        }
        </div>
    )
    }
}

IconButton.propTypes = {
    hover: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.bool
      ]),
    color: PropTypes.string,
    icon: PropTypes.string.isRequired,
    fontSize: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
      ]),
    disabled: PropTypes.bool
}

export default IconButton;
