
const graphColors =  ['#FF7CFF', '#CF2556', '#8F2B6C', '#FF690D', '#FFBE50'];

export const theme = {
    menuHeight: 67,
    gameMenuHeight: 67,
    fontSize: '16px',
    animS: '0.5s',
    animM:'1s',
    animL:'2s',
    spaceS: '3',
    spaceM: '5',
    spaceL: '10',
    mediaM: '650px',
    mediaS: '400px',
    mediaL: '900px',
    remXS: '0.75em',
    remS: '1em',
    remM: '1.5em',
    remL: '2em',
    remXL: '3em',
    iconS: '30px',
    iconM: '45px',
    iconL: '60px',
    z0: '-1',
    z1: '0',
    z2: '1',
    z3: '2',
    z4: '3',
    correct: '#0EBE2D',
    problem: '#C9002C',
    primaryL: '#ff69b4',
    // primary: '#ff69b4',
    // primaryD: '#872B5F',
    //yellow
    // primary: '#FFF38D',
    // primaryD: '#E35D1A',
    //green
    // primary: '#8CFFAB',
    // primaryD: '#0F9982',
    //blue
    // primary: '#25D8DA',
    // primaryD: '#0A5474',
    //pink
    docText: 'black',
    docPrimary: '#4600A6',
    docSecondary: '#C4184E',
    docTertiary: '#9A00A6',
    error: 'orange',
    primary: '#F379C3',
    primaryD: '#BF046E',
    
    primaryO: '#ff69b488',
    secondaryL: '#EDE32E',
    // secondary: '#EDE32E',
    // secondaryD: '#EDB82E',
    secondary: '#FFF38D',
    secondaryD: '#E35D1A',
    secondaryO: '#EDE32E44',
    g1: '#FFF38D',
    g2: '#8CFFAB',
    g3: '#25D8DA',
    g4: '#F28FC1',
    gChoice: '#4F0465',
    mc: 'pink',
    oc1: 'cyan',
    bgPP: `#22005C`,
    correct: '#0EBE2D',
    incorrect: 'red',
    button: '#353838',
    neutralL: '#1F2121',
    neutral: '#131414',
    neutralO: '#0F0F0Fcc',
    neutralD: '#000000',
    text: '#FCFEE0',
    textInverse: 'black',
    textBg: '#000000bb',
    zBehind: '-1',
    zBase: '0',
    zMid: '1',
    zTop: '2',
    zUi: '3',
   
    graphColors,
    getGraphColor: (index) => {
        return graphColors[index%graphColors.length]
    },
    getColor: (theme, color, fallback) => {
        if(theme[color]){
            return theme[color];
        }else if(color && color !== true){
            return color;
        }else{
            if(fallback){
                return theme[fallback] || fallback;
            }
            return theme.text;
        }
    },
    iconSem: '1.5em',
    iconMem: '2.5em',
    iconLem: '3.5em',
    getWidth: (theme, width, fallback) => {
        if(theme[width]){
            return theme[width];
        }else if(width && width !== true){
            return width;
        }else{
            if(fallback){
                return theme[fallback] || fallback;
            }
            return theme.mediaL;
        }
    },
    getFontSize: (theme, size, fallback) => {
        if(theme[size]){
            return theme[size];
        }else if(size && size !== true){
            return size + 'em';
        }else{
            if(fallback) return theme[fallback]?theme[fallback]:fallback+'em';
            return theme.remS;
        }
    }
}

